<script>
import { Bar } from 'vue-chartjs'
  export default {
    extends: Bar,
    props: {
      chartData: {
        type: Array | Object,
        required: false
      },
       options: {
        type: Array | Object,
        required: false
      },
    },
    data () {
      return {
        gradient:null
      }
    },
    // vue-chart-js
    mounted () {
      this.renderChart(this.chartData, this.options)
    }
  }
</script>
