<template>
  <div
    class="all-chart check-price-step"
    ref="allChart"
    :class="{ 'all-chart--expanded': isExpanded }"
  >
    <b-button
      @click="toggleChart"
      class="chart-icon d-flex align-items-center justify-content-center"
      v-b-tooltip.hover.top="$t('common.viewStatisticsTooltip')"
    >
      <svg-icon name="leaderboard-black" class="graph-icon"></svg-icon>
      <span class="sr-only">{{
        $t('property.toggleOverallViewOnMarketStatistics')
      }}</span>
    </b-button>
    <b-overlay :show="loading.searchLandscape" class="chart-container">
      <button
        @click="toggleChart"
        class="hide d-flex border-0 p-0 bg-transparent"
      >
        {{ $t('common.hide') }}
        <svg-icon
          name="black-arrow-right"
          class="arrow-custom"
          fill="#000"
        ></svg-icon>
      </button>
      <!-- start bar-chart -->
      <div class="chart-wrapper" v-if="isExpanded">
        <h2 class="mb-0">{{ $t('property.marketLandscape') }}</h2>
        <p>{{ $t('property.overallViewOnMarketStatistics') }}</p>
        <BarChart
          v-if="
            barChartData && barChartData.labels && barChartData.labels.length
          "
          :chartData="barChartData"
          :height="220"
          :options="options"
        />
        <!-- end bar-chart -->
      </div>
    </b-overlay>
  </div>
</template>

<script>
import BarChart from './chart-partials/BarChart'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('SearchModule')

export default {
  name: 'AllPropertiesChart',
  components: { BarChart },
  data() {
    return {
      isExpanded: false,
      barChartData: {
        labels: [],
        datasets: [
          {
            barPercentage: 0.8,
            label: '',
            backgroundColor: '#7cb5ec',
            data: [],
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          position: 'top',
          display: false,
        },
        tooltip: {
          usePointStyle: true,
        },
        scales: {
          yAxes: [
            {
              gridLines: {
                display: false,
              },
              ticks: {
                beginAtZero: true,
              },
            },
          ],
          xAxes: [
            {
              gridLines: {
                display: false,
              },
            },
          ],
        },
      },
    }
  },
  computed: {
    // searchLandscape() {
    //   return this.$store.state.SearchModule.searchLandscape
    // },
    loading() {
      return this.$store.state.SearchModule.loading
    },
  },
  methods: {
    // ...mapActions(['getSearchLandScape']),
    toggleChart() {
      this.isExpanded = !this.isExpanded

      // if (!this.searchLandscape) {
      //   this.getChartData()
      // }
    },
    // getChartData() {
    //   this.getSearchLandScape()
    // },
  },
  watch: {
    // searchLandscape(newVal) {
    //   this.barChartData.labels = this.searchLandscape.xAxis
    //   this.barChartData.datasets[0].data = this.searchLandscape.yAxis
    // },
  },
}
</script>

<style lang="scss" scoped>
.all-chart {
  position: fixed;
  top: 40%;
  right: 0;
  z-index: 99;
  background: transparent;
  @include media-breakpoint-down(md) {
    top: 53%;
  }
  .chart-container {
    margin-right: 10px;
    position: relative;
    width: 0;
    overflow: hidden;
    padding: 0;
    transition: all 0.08s ease-in-out;
    transform: translateY(-30%);
    border-radius: 7px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #fff;

    span {
      position: absolute;
      top: -10px;
      cursor: pointer;
      right: 0;
      background: $mainColorLight;
      color: #fff;
      padding: 3px;
      border-radius: 15%;
      font-size: 18px;
    }
  }
  &--expanded {
    .btn {
      margin-inline-start: -44px;
    }
    .chart-container {
      width: 796px;
      overflow: visible;
      max-width: 100vw;
      padding: 25px;
    }
  }
  .hide {
    position: absolute;
    right: 20px;
  }
  h2 {
    font-size: 22px;
    font-weight: bold;
    text-align: center;
    & + p {
      color: $secondaryTextColor;
      text-align: center;
      font-size: 14px;
    }
  }
  .arrow-custom {
    width: 24px;
    height: 24px;
    [dir='rtl'] & {
      transform: rotate(180deg);
    }
  }
  .chart-icon {
    width: 45px;
    height: 66px;
    padding: 20px 0px;
    background-color: $secondaryTextColor;
    .graph-icon {
      width: 24px;
      height: 24px;
    }
    @include media-breakpoint-down(md) {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
  }
}
</style>
