<template>
  <div class="filter-options">
    <div>
      <span class="properties-count">
        {{ `${propertiesLength} ${$t('property.propertiesFound')}` }}
      </span>
    </div>
    <div class="desktop-view">
      <!-- <FilterByCity class="mx-2" /> -->
      <!-- <PropertySortTerm class="mx-2" /> -->
      <PropertySort @getSortedProperties="getSortedProperties($event)" class="mx-2" />
      <PropertyFilter
        @getFilteredProperties="getFilteredProperties($event)"
        class="mx-2"
        :related="propertyRelated" />
        <!-- :insideCompound="insideCompound" -->
    </div>
  </div>
</template>

<script>
import FilterByCity from '@/components/property/FilterByCity';
import PropertyFilter from '@/components/property/PropertyFilter';
import PropertySort from '@/components/property/PropertySort';
import PropertySortTerm from '@/components/property/PropertySortTerm';
export default {
  components: {
    FilterByCity,
    PropertyFilter,
    PropertySort,
    PropertySortTerm,
  },
  props: ['propertiesLength'],
  data() {
    return {
      propertyRelated: null,
    };
  },
  methods:{
    getSortedProperties(e){
      this.$emit('getSortedProperties', e)
    },
    getFilteredProperties(e){
      this.$emit('getFilteredProperties', e)
    }
  }
};
</script>

<style lang="scss" scoped>
.filter-options {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 52px 16px 0;

  .properties-count {
    color: #111;
    font-weight: bold;
    font-size: 16px;
    line-height: 16px;
  }

  .desktop-view {
    display: flex;
  }
}

@media screen and (max-width: 768px) {
  .filter-options {
    padding: 22px 16px 0;

    .desktop-view {
      display: none;
    }
  }
}
</style>
