<template>
  <div class="all-compare" :class="{ 'all-compare--expanded': compare }">
    <!-- compare list btn  -->
    <!-- <b-button
      @click="toggleChart"
      class="compare-btn"
      v-b-tooltip.hover.top="$t('common.viewCompareTooltip')"
      v-if="isExpanded">
      {{$t('property.compare_list')}} ({{compareData.length}})
    </b-button> -->
    <div class="compare compare-container" v-if="compare">
      <div>
        <div
          class="
            d-block d-md-flex
            justify-content-between
            align-items-center
            w-100
          "
        >
          <h5>{{ $t('property.compareHeading') }}</h5>
          <b-button class="hide-btn" @click="hideCompare"
            >{{ $t('common.hide') }}
            <svg-icon class="arrow-custom" name="black-arrow" />
          </b-button>
        </div>
        <div class="w-100 py-3">
          <b-row v-if="compareData.length">
            <b-col
              cols="3"
              class="compare-cotent"
              v-for="(compareItem, i) in compareData"
              :key="i"
            >
              <span
                class="delete"
                @click="deleteProperty(compareItem.property_id)"
                >x</span
              >
              <AppImage
                :src="compareItem.property_image[0]"
                alt="property image"
                class="object-fit-cover"
              />
            </b-col>
          </b-row>
          <div v-else>{{ $t('property.compareEmpty') }}</div>
        </div>
        <div
          class="d-flex justify-content-between align-items-center w-100"
          v-if="compareData.length"
        >
          <b-button variant="outline-primary" @click="clearAll">{{
            $t('common.clearAll')
          }}</b-button>
          <b-button variant="primary" @click="openComapreModal">{{
            $t('common.compare')
          }}</b-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'propertyCompare',
  props: ['compareData', 'open', 'isExpanded'],
  data() {
    return {
      compare: false,
      compareCotent: [],
      compareValue: 0,
    }
  },
  methods: {
    toggleChart() {
      this.compare = !this.compare
    },
    hideCompare() {
      this.compare = false
    },
    clearAll() {
      this.$emit('clearAll')
    },
    deleteProperty(property_id) {
      this.$emit('deleteProperty', property_id)
    },
    openComapreModal() {
      this.hideCompare()
      this.compareValue++
      let val = this.compareValue
      this.$store.dispatch('PropertyModule/openComapreModal', val)
    },
  },
  watch: {
    open() {
      this.compare = true
    },
  },
}
</script>

<style lang="scss" scoped>
.all-compare {
  position: fixed;
  bottom: 20px;
  z-index: 80;
  background: transparent;
  left: 50%;
  transform: translateX(-50%);
  .compare-container {
    margin-right: 10px;
    position: relative;
    overflow: hidden;
    padding: 0;
    transition: all 0.08s ease-in-out;
    transform: translateY(-30%);
    border-radius: 7px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #fff;
  }
  .compare-btn {
    background: #204ECF;
    border-radius: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
  }
  &--expanded {
    .compare-btn {
      /* margin-inline-start: -45px; */
      margin-inline-start: 20px;
    }
    .compare-container {
      overflow: visible;
      padding: 25px;
    }
  }
  .hide-btn {
    background: $mainColorLowLight;
    border: 0;
    font-size: 14px;
  }
  .arrow-custom {
    [dir='rtl'] & {
      transform: rotate(180deg);
    }
  }
  svg {
    width: 20px;
    height: 20px;
  }
}

.compare {
  position: fixed;
  width: fit-content;
  right: 0;
  z-index: 99;
  top: 55%;
  min-width: 415px;
  height: auto;
  min-height: 278px;
  max-width: 100%;
  padding: 10px;
  margin-right: 10px;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 0 5px $mainColorLight;
  .compare-cotent {
    position: relative;
    width: 135px;
    display: inline-block;
    ::v-deep() img {
      width: 100%;
      height: 120px;
      border-radius: 5px;
    }
  }
  span.delete {
    color: #fff;
    background: $mainColor;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 15px;
    border-radius: 50%;
    position: absolute;
    right: 3px;
    top: 3px;
    cursor: pointer;
  }
}
@include media-breakpoint-down(md) {
  .compare {
    top: auto;
    bottom: 0;
    min-width: 250px;
    .compare-cotent {
      width: 100px;
      ::v-deep() img {
        height: 90px;
      }
    }
  }
}
@include media-breakpoint-down(sm) {
  .compare {
    .compare-cotent {
      width: 70px;
      ::v-deep() img {
        height: 60px;
      }
    }
  }
}
</style>
