<template>
  <div class="comapare" ref="comapare" @click.self="closeCompare">
    <div class="modal-content">
      <b-row class="border-bottom mt-4">
        <b-col cols="2">
          <div class="text-center">
            <svg-icon
              class="close-compare pr-1 pl-1"
              @click="closeCompare"
              name="close-black" />
          </div>
        </b-col>

        <b-col cols="7" class="text-center">
          <div>
            <h2>
              {{ $t('property.compareProperty') }}
            </h2>
          </div>
        </b-col>

        <b-col cols="3">
          <div class="text-center">
            <button class="clear-btn" @click="clearAll">
              {{ $t('property.clear') }}
            </button>
          </div>
        </b-col>
      </b-row>

      <div class="container compare-property">
        <swiper
          class="swiper gallery-thumbs"
          :options="swiperOptionThumbs"
          ref="swiperThumbs">
          <swiper-slide
            v-for="(property, i) in compareData"
            :key="i"
            class="border">
            <div class="p-15">
              <div class="compare-card">
                <p class="text-capitalize property-name">
                  {{ property.property_name }}
                </p>
                <div class="carousel-wrapper">
                  <div
                    class="delete"
                    @click="deleteColumn(property.property_id)">
                    x
                  </div>
                  <div class="favorite">
                    <svg-icon name="unchecked-heart" />
                  </div>
                  <b-carousel fade indicators controls :interval="0">
                    <b-carousel-slide
                      class="gallery"
                      v-for="(img, i) in property.property_image"
                      :key="i">
                      <template #img>
                        <AppImage
                          :src="img"
                          :height="250"
                          class="object-fit-contain bg-dark" />
                      </template>
                    </b-carousel-slide>
                  </b-carousel>
                </div>
                <b-button
                  variant="primary"
                  class="w-100 text-center"
                  @click="bid(property.property_id)">
                  {{ $t('common.sendOffer') }}
                </b-button>
                <div class="py-3">
                  <h2 class="mt-1 mb-3">
                    {{
                      property.favourite_term == 0
                        ? property.daily_price
                        : property.monthly_price
                    }}
                    {{ $t('common.egp') }} /
                    {{
                      property.favourite_term == 0
                        ? $t('common.night')
                        : $t('common.month')
                    }}
                  </h2>
                  <p class="p-small">
                    {{ property.property_slug }}
                  </p>
                </div>
                <hr />
                <div class="">
                  <p class="p-small">
                    {{ $t('property.propertyArea') }}:
                    {{ property.property_area }} {{ $t('property.meter') }}
                    <sup>2</sup>
                  </p>
                  <hr />
                  <p class="p-small">
                    {{ $t('property.noBedroom') }}: {{ property.no_bathrooms }}
                  </p>
                  <hr />
                  <p class="p-small">
                    {{ $t('property.noBathrooms') }}:
                    {{ property.no_of_bedroom }}
                  </p>
                  <hr />
                </div>

                <ul class="amenity-list" v-if="property.amenities">
                  <li
                    v-for="(amenity, i) in property.amenities"
                    :key="`$amenityProperty${i}`"
                    :class="{ active: amenity.istrue == 1 }">
                    {{ amenity.name }}
                  </li>
                </ul>
              </div>
              <div class="" v-if="property.review.length">
                <p>{{ $t('property.whatOtherSaying') }}</p>
                <div class="h-100 mb-4">
                  <div class="what-say mb-3">
                    <b-carousel
                      fade
                      indicators
                      img-width="100%"
                      img-height="480"
                      :interval="0">
                      <b-carousel-slide
                        img-src="@/assets/images/property/brief-bg.png"
                        v-for="(review, i) in property.review"
                        :key="i">
                        <div class="carousel-caption">
                          <div>
                            <div class="head mb-2">
                              <div class="row align-items-center">
                                <div class="col-4">
                                  <div class="user-image">
                                    <AppImage
                                      :src="review.profile_picture"
                                      :width="100"
                                      :height="100"
                                      alt="user image" />
                                  </div>
                                </div>
                                <div class="col-8 px-1">
                                  <p class="mb-1 text-left">
                                    {{ review.user_name }}
                                  </p>
                                  <span class="d-block text-left">
                                    {{ review.human_creation_date }}
                                  </span>
                                </div>
                              </div>
                            </div>
                            <q>{{ review.note }}</q>
                          </div>
                        </div>
                      </b-carousel-slide>
                    </b-carousel>
                  </div>
                  <b-button
                    variant="primary"
                    class="w-100 d-block"
                    @click="bid(property.property_id)">
                    {{ $t('property.bidNow') }}
                  </b-button>
                </div>
              </div>
              <div class="" v-else>
                {{ $t('property.noReviewYet') }}
              </div>
            </div>
          </swiper-slide>
          <div
            class="swiper-nav-button swiper-button-next swiper-button-white"
            slot="button-next"></div>
          <div
            class="swiper-nav-button swiper-button-prev swiper-button-white"
            slot="button-prev"></div>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
export default {
  name: 'CompareModal',
  props: ['compareData'],
  data() {
    return {
      swiperOptionThumbs: {
        slidesPerView: 'auto',
        loopedSlides: 0,
        spaceBetween: 10,
        loop: false,
        touchRatio: 0.2,
        slideToClickedSlide: true,
        observer: true,
        observeParents: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        breakpoints: {
          1200: {
            slidesPerView: 4,
          },
          900: {
            slidesPerView: 2,
          },
          450: {
            slidesPerView: 1.2,
          },
        },
      },
    };
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  computed: {
    openModal() {
      return this.$store.state.PropertyModule.openCmpareModal;
    },
  },
  methods: {
    closeCompare() {
      this.$refs.comapare.classList.remove('d-block');
      document.getElementsByTagName('body')[0].style.overflow = 'scroll';
    },
    deleteColumn(property_id) {
      this.$emit('deleteColumn', property_id);
    },
    bid(id) {
      this.closeCompare();
      this.$store.commit('PropertyModule/addBid', id);
    },
    clearAll() {
      this.$store.dispatch('PropertyModule/clearCompareProperty');
      this.$emit('clearAll');
    },
  },
  watch: {
    openModal() {
      this.$refs.comapare.classList.add('d-block');
      document.getElementsByTagName('body')[0].style.overflow = 'hidden';
    },
  },
};
</script>

<style lang="scss" scoped>
.comapare {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ccccccc4;
  z-index: 99999999;
  padding: 10px 20px 30px;
  display: none;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar-track {
    background: rgb(143, 139, 139);
  }
  &::-webkit-scrollbar-thumb {
    background: #fff;
  }
}
.close-compare {
  width: 30px;
  height: 24px;
  color: $mainColor;
  cursor: pointer;
}
.modal-content {
  min-height: 100%;
  .clear-btn {
    border: none;
    padding: 0 5px;
    background-color: transparent;
  }
}
h2 {
  font-size: 20px;
  font-weight: 800;
}
p {
  font-size: 15px;
  text-transform: capitalize;
  font-weight: 800;
}
p.p-small {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: $secondaryTextColor;
  text-transform: capitalize;
}
hr {
  border-color: #ccc;
}
.compare-property {
  .compare-card {
    margin-bottom: 30px;
    .property-name {
      min-height: 45px;
    }
  }
  .status {
    background: rgba(0, 128, 253, 0.1098);
    border-color: $mainColorLight;
    color: $mainColorLight;
    padding: 5px 8px;
    border-radius: 5px;
    text-transform: capitalize;
  }
  .carousel-wrapper {
    height: 176px;
    position: relative;
    margin-top: 10px;
    margin-bottom: 30px;
    ::v-deep() .carousel-inner {
      border-radius: 10px;
    }
    ::v-deep() .carousel-indicators {
      margin-bottom: 2px;
    }
    ::v-deep() .carousel-indicators li {
      width: 10px;
      height: 10px;
      border-radius: 50%;
    }
    .delete,
    .favorite {
      position: absolute;
      z-index: 9;
    }
    .delete {
      top: 0;
      transform: translateY(-10px);
      right: -3px;
      width: 25px;
      height: 25px;
      text-align: center;
      line-height: 20px;
      border-radius: 50%;
      background: #004ed4;
      color: #fff;
      border: 1px solid #fff;
      cursor: pointer;
    }
    .favorite {
      top: 10px;
      right: 20px;
      svg {
        width: 20px;
        height: 20px;
      }
    }
  }
}
.amenity-list {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    margin: 0 20px 10px;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-40%);
      left: -35px;
      background-image: url('@/assets/images/property/unchecked.png');
      background-position: 0 0;
      background-repeat: no-repeat;
      background-size: 19px;
      width: 20px;
      height: 20px;
    }
  }
  li.active::before {
    background-image: url('@/assets/images/property/checked.png');
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 19px;
    width: 20px;
    height: 20px;
  }
}
.compare-property .what-say {
  ::v-deep() .carousel.slide {
    height: 100%;
    min-height: 300px;
  }
  ::v-deep() .carousel-inner {
    border-radius: 10px;
  }
  ::v-deep() .carousel-indicators {
    margin: 10px 0;
    position: relative;
  }
  ::v-deep() .carousel-indicators li {
    border: 0;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: $mainColor;
  }
  ::v-depp img.img-fluid {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
  ::v-deep() .carousel-caption {
    width: 100%;
    padding: 0;
    right: 0;
    left: 0;
    bottom: 0;
    font-size: 15px;
    top: 0;
    padding: 10px;
    text-shadow: 0 0 black;
    text-align: left;
    color: $mainTextColor;
    > div {
      position: relative;
      width: 100%;
      height: 100%;
      .head .user-image {
        position: relative;
        padding-top: 100%;
        ::v-deep() p,
        ::v-deep() span {
          font-style: normal;
        }
        img {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          border-radius: 50%;
        }
      }
    }
  }
}
::v-deep() .carousel-item.gallery {
  img {
    height: 176px;
    min-width: 100%;
  }
}
[dir='rtl'] {
  .compare-property {
    .carousel-wrapper {
      .delete {
        right: auto;
        left: -3px;
      }
    }
  }
}
[dir='rtl'] .text-left {
  text-align: right !important;
}
.p-15 {
  padding: 15px;
}
.swiper {
  &.gallery-thumbs {
    height: 112px;
    box-sizing: border-box;
    ::v-deep() {
      .swiper-slide {
        opacity: 0.3;
        overflow: hidden;
      }
      .swiper-slide-active {
        opacity: 1;
      }
    }
  }
  .swiper-nav-button {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    color: #000;
    background-color: #fff;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.47);
    font-style: 12px;
    top: 50vh;
    position: fixed;
    &:after {
      font-size: 14px;
      font-weight: 700;
    }
  }
}
::v-deep() .swiper.gallery-thumbs .swiper-slide {
  opacity: 1;
  height: auto;
}
::v-deep() .swiper.gallery-thumbs {
  height: auto;
}
</style>
