<template>
  <div class="">
    <no-ssr>
      <VueSimpleRangeSlider
        class="range"
        :min="val[0]"
        :max="val[1]"
        :logarithmic="true"
        v-model="range"
      />
    </no-ssr>
  </div>
</template>
<script>
import 'vue-simple-range-slider/dist/vueSimpleRangeSlider.css'
export default {
  name: 'RangeInputSlider',
  props: {
    val: {
      type: Array,
      require: true,
    },
  },
  computed: {
    range: {
      get() {
        return this.val
      },
      set(val) {
        this.$emit('input', val)
      },
    },
  },
}
</script>
<style scoped lang="scss">
.range {
  width: 100%;
}
::v-deep()
  .simple-range-slider
  .simple-range-slider-bg-bar
  .simple-range-slider-anchor
  .simple-range-slider-popover-left,
::v-deep() .simple-range-slider .simple-range-slider-popover {
  display: none;
}

[dir='rtl'] .custom-range .simple-range-slider-bg-bar {
  direction: rtl !important;
}
</style>
