<template>
  <div class="property-filter-wrapper">
    <div class="filter-btn" @click="activeRange" v-b-toggle.sidebar-right>
      <svg-icon
        name="black-filter"
        viewBox="0 0 20.053 20.053"
        class="filter-icon"
        height="24px" />
      <span class="filter-text">
        {{ $t('property.filter') }}
      </span>
    </div>
    <b-sidebar id="sidebar-right" title="" right shadow>
      <h1 class="filter-title">{{ $t('property.filter') }}</h1>
      <!-- start bar-chart -->
      <b-overlay :show="loading.searchGraph" class="chart-container">
        <!-- <bar-chart :data="barChartData" :options="barChartOptions"></bar-chart> -->
        <BarChart
          v-if="barChartData && barChartData.labels && barChartData.labels.length"
          :chartData="barChartData"
          :height="220"
          :options="barChartOptions" />
      </b-overlay>
      <!-- end bar-chart -->

      <hr />
      <div class="price">
        <h2>{{ $t('property.propertyPrice') }}</h2>
        <RangeInputSlider
          :val="filterPrice"
          @input="updatePriceValue($event)"
          v-if="range" />
        <div class="row">
          <div class="col-6">
            <div class="price-min-max">
              <p class="mb-0">
                {{ $t('property.minPrice') }} : {{ filterPrice[0] }}
              </p>
            </div>
          </div>
          <div class="col-6">
            <div class="price-min-max">
              <p class="mb-0">
                {{ $t('property.maxPrice') }} : {{ filterPrice[1] }}
              </p>
            </div>
          </div>
        </div>
      </div>
      <hr />
      <div class="rooms">
        <h2>{{ $t('property.rooms') }}</h2>
        <div class="row">
          <div class="col-12">
            {{ $t('property.bathrooms') }}
          </div>
          <div class="col-12 custome-input">
            <b-form-group>
              <b-form-radio-group>
                <b-form-radio v-model="bathNumber" :key="0" :value="0">{{
                  $t('property.any')
                }}</b-form-radio>
                <b-form-radio
                  v-model="bathNumber"
                  v-for="i in 4"
                  :key="i"
                  :value="i"
                  >{{ i }}</b-form-radio
                >
              </b-form-radio-group>
            </b-form-group>
          </div>
          <div class="col-12">
            {{ $t('property.bedrooms') }}
          </div>
          <div class="col-12 custome-input">
            <b-form-group>
              <b-form-radio-group>
                <b-form-radio v-model="bedNumber" :key="0" :value="0">{{
                  $t('property.any')
                }}</b-form-radio>
                <b-form-radio
                  v-model="bedNumber"
                  v-for="i in 4"
                  :key="i"
                  :value="i"
                  >{{ i }}</b-form-radio
                >
              </b-form-radio-group>
            </b-form-group>
          </div>
        </div>
      <hr />
      </div>
      <div class="custome-input" v-if="related && related.propertyType">
        <h2>{{ $t('property.propertyType') }}</h2>
        <b-form-group>
          <b-form-checkbox-group>
            <b-form-checkbox
              v-model="selectedProperty"
              v-for="(type, i) in related.propertyType"
              :key="i"
              :value="type.id"
              >{{ type.name }}</b-form-checkbox
            >
          </b-form-checkbox-group>
        </b-form-group>
      <hr />
      </div>
      <div class="custome-input" v-if="related && related.furnishType">
        <h2>{{ $t('property.furnishType') }}</h2>
        <b-form-group v-slot="{ ariaDescribedby }">
          <b-form-radio
            v-for="(furnish, i) in related.furnishType"
            :key="i"
            v-model="furnishType"
            :aria-describedby="ariaDescribedby"
            name="furnish"
            class="radio-button"
            :value="furnish.id"
            >{{ furnish.name }}</b-form-radio
          >
        </b-form-group>
      <hr />
      </div>

      <div class="amenities" v-if="related && related.amenities">
        <h2>{{ $t('property.amenities') }} {{ related.amenities }}</h2>
        <b-col
          cols="12"
          class="p-0 amenities-container"
          v-for="(amenities, categoryName) in related.amenities"
          :key="categoryName">
          <p>{{ categoryName }}</p>

          <!-- :class="{ 'col-md-6': messaging }"  -->
          <b-col
            class="d-inline-block p-0 col-md-6 col-6"
            v-for="amenity in amenities"
            :key="amenity.id">
            <AmenityOption
              :amenity="amenity"
              :hasCheckbox="true"
              v-model="selectedAmenities" />
          </b-col>
        </b-col>
      <hr />
      </div>
      <div class="clear-apply">
        <b-button class="clear-button" @click="clearData">{{
          $t('property.clear')
        }}</b-button>
        <b-button
          variant="primary"
          @click="applyFilterDataNew"
          v-b-toggle.sidebar-right
          >{{ $t('property.apply') }}</b-button
        >
      </div>
    </b-sidebar>
  </div>
</template>
<script>
import CounterInput from '@/components/shared/CounterInput';
import RangeInputSlider from '@/components/shared/RangeInputSlider';
import { createNamespacedHelpers } from 'vuex';
const { mapState: mapPropertyState, mapActions: mapPropertyAction } =
  createNamespacedHelpers('PropertyModule');
const { mapState: mapSearchState, mapActions: mapSearchAction } =
  createNamespacedHelpers('SearchModule');

import BarChart from './chart-partials/BarChart';
import AmenityOption from '@/components/shared/AmenityOption';

export default {
  name: 'PropertyFilter',
  components: {
    CounterInput,
    RangeInputSlider,
    BarChart,
    AmenityOption,
  },
  props: ['selectedPropertyValue', 'insideCompound', 'related'],

  data() {
    return {
      range: false,
      barChartData: {
        labels: [],
        datasets: [
          {
            barPercentage: 0.8,
            label: '',
            backgroundColor: '#7cb5ec',
            data: [],
          },
        ],
      },
      barChartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          position: 'top',
          display: false,
        },
        tooltip: {
          usePointStyle: true,
        },
        scales: {
          yAxes: [
            {
              gridLines: {
                display: false,
              },
              ticks: {
                beginAtZero: true,
              },
            },
          ],
          xAxes: [
            {
              gridLines: {
                display: false,
              },
            },
          ],
        },
      },
      bathNumber: 1,
      bedNumber: 1,
      furnishType: '',
      selectedAmenities: [],
      sort: 'plh',
      filterPrice: [0, 100000],
    };
  },
  created() {
    this.getChartData();
  },
  computed: {
    ...mapSearchState(['searchLandscape']),
    ...mapPropertyState({
      sortValue: (state) => state.filteredProperty.params.sort,
    }),
    searchGraph() {
      return this.$store.state.SearchModule.searchGraph;
    },
    filteredPropertyParams() {
      return this.$store.state.PropertyModule.filteredProperty.params;
    },
    loading() {
      return this.$store.state.SearchModule.loading;
    },
    selectedProperty: {
      get() {
        return this.selectedPropertyValue;
      },
      set(val) {
        this.$emit('input', val);
      },
    },
    watchDataGraph() {
      return [
        this.selectedProperty,
        this.bathNumber,
        this.bedNumber,
        this.furnishType,
        this.selectedAmenities,
        this.sort,
        // this.insideCompound,
      ];
    },
    watchDatafilter() {
      return [this.selectedProperty, this.sort]; // , this.insideCompound
    },
  },
  beforeDestroy() {
    this.resetFilterData();
  },
  methods: {
    ...mapPropertyAction(['getSearchProperty', 'resetFilterData']),
    ...mapSearchAction(['getSearchLandScape']),

    // applyFilterData() {
    //   let amenitiesSelected = this.selectedAmenities.reduce(
    //     (amenities, selectedAmenity) => {
    //       if (!amenities[selectedAmenity['category']]) {
    //         amenities[selectedAmenity['category']] = [];
    //       }
    //       amenities[selectedAmenity['category']].push(selectedAmenity.id);
    //       return amenities;
    //     },
    //     {}
    //   );

    //   let content = {
    //     inCompound: this.insideCompound,
    //     pt: this.selectedProperty,
    //     bath: this.bathNumber,
    //     bed: this.bedNumber,
    //     amenity: amenitiesSelected,
    //     price: {
    //       min: this.filterPrice[0],
    //       max: this.filterPrice[1],
    //     },
    //     ...(this.furnishType != '' ? { pf: this.furnishType } : {}),
    //     filter: false,
    //   };

    //   this.$emit('filter', content);
    //   this.getSearchProperty(content);
    //   this.getChartData(content);
    // },
    async applyFilterDataNew(){
      let filterParams = {
        location_id: this.$store.state.SearchModule.searchLocationId || localStorage.getItem('searchLocationId'),
        bathroom: this.bathNumber,
        bedroom: this.bedNumber,
        min_daily_price: this.filterPrice[0],
        max_daily_price: this.filterPrice[1],
        min_monthly_price: this.filterPrice[0],
        max_monthly_price: this.filterPrice[1]
      }
      var { Data } = await this.$axios.$get(`/searchProperty`, {
        params: {
          ...filterParams
        }
      });
      this.$emit('getFilteredProperties', Data)
    },
    clearData() {
      this.insideCompound = 0;
      this.selectedProperty = [];
      this.furnishType = '';
      this.bathNumber = 0;
      this.bedNumber = 0;
      this.selectedAmenities = [];
      this.sort = 'plh';
      this.filterPrice = [0, 100000];
      let content = {
        filter: false,
      };
      this.getSearchProperty(content);
      this.getChartData(content);
    },
    getChartData(content) {
      const search_text = this.$route.query.keyword ? '?keyword=' + this.$route.query.keyword : Object.values(this.$route.params).join('/');

      this.$store
        .dispatch('SearchModule/getSearchGraph', {
          search_text: search_text,
          ...content,
          ...this.filteredPropertyParams,
        })
        .then(
          (res) => {
            this.barChartData.labels = this.searchGraph.xAxis;
            this.barChartData.datasets[0].data = this.searchGraph.yAxis;
          },
          (err) => {}
        );
    },
    activeRange() {
      this.range = true;
      // if (!this.searchLandscape) {
      //   this.SearchLandScapeData();
      // }
    },
    updatePriceValue(content) {
      this.filterPrice = content;
    },
    // SearchLandScapeData() {
    //   this.getSearchLandScape();
    // },
  },
  watch: {
    sortValue(newValue) {
      if (newValue == null) {
        this.sort = 'plh';
      } else {
        this.sort = newValue;
      }
    },
    watchDataGraph() {
      this.getChartData();
    },
    watchDatafilter() {
      this.applyFilterData();
    },
    // searchLandscape(newVal) {
    //   this.filterPrice = [
    //     this.searchLandscape.price.min,
    //     this.searchLandscape.price.max,
    //   ];
    // },
  },
};
</script>

<style lang="scss" scoped>
.property-filter-wrapper {
  width: 170px;

  .filter-btn {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 6px 12px;
    color: #111;
    font-weight: bold;
    background-color: transparent;
    border: 2px solid #111;
    border-radius: 8px;
  }
}

.filter-title {
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
}
.price-min-max {
  width: 100%;
  height: auto;
  border: 1px solid $grayLightColor;
  border-radius: 5px;
  padding: 5px;
  color: $grayColor;
}
::v-deep() .filter-button {
  height: 45px;
  width: 100px;
  display: flex;
  justify-content: space-around;
  @include media-breakpoint-down(sm) {
    justify-content: center;
  }
  align-items: center;
  // margin-left: 15px;
  /* &:hover {
    background: #fff;
    color: $mainColor;
  } */
}
::v-deep() .b-sidebar.b-sidebar-right {
  background-color: #02020238 !important;
  width: 100%;
}
::v-deep() .b-sidebar-body,
::v-deep() .b-sidebar-header {
  width: 460px;
  background: #fff;
  margin-left: auto;
  max-width: 100%;
  padding: 10px 20px;
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar-track {
    background: #fff;
  }
  &::-webkit-scrollbar-thumb {
    background: #fff;
  }
}
.clear-apply {
  display: flex;
  justify-content: space-between;
  padding: 13px 0;
  margin: 10px 0;
  .btn {
    width: 95px;
    height: 38px;
    border-radius: 5px;
  }
  ::v-deep() .clear-button {
    border-color: $grayLightColor;
    color: #000;
    background: #fff;
  }
}
h2 {
  color: #000;
  font-size: 18px;
  font-weight: 600;
  text-transform: capitalize;
  margin-bottom: 20px;
}
::v-deep() .custome-input .custom-control {
  padding: 0;
  margin: 0;
}
::v-deep() .custome-input .custom-control-label {
  margin: 5px 8px;
  border: 1px solid $grayLightColor;
  font-size: 14px;
  color: $grayColor;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 12px;
  gap: 10px;
  background: #ffffff;
  border-radius: 8px;
}
::v-deep() .custome-input .custom-control-input:checked ~ .custom-control-label,
::v-deep()
  .custome-input
  .custom-checkbox
  .custom-control-input:checked
  ~ .custom-control-label {
  background: #204ecf;
  border-radius: 8px;
  color: #ffffff;
  padding-left: 20px;
}
::v-deep()
  .custome-input
  .custom-control-input:checked
  ~ .custom-control-label:after,
::v-deep()
  .custome-input
  .custom-checkbox
  .custom-control-input:checked
  ~ .custom-control-label:after {
  content: '\2713';
  display: inline-block;
  color: white;
  padding: 0 6px 0 0;
  left: 5px;
  background-image: none;
  top: 50%;
  transform: translateY(-50%);
}
::v-deep() .custome-input .custom-radio .custom-control-label::before,
::v-deep()
  .custome-input
  .custom-control-input:checked
  ~ .custom-control-label::before,
::v-deep() .custome-input .custom-control-label::after,
::v-deep() .custome-input .custom-checkbox .custom-control-label::before {
  display: none;
}
::v-deep() .custome-input .radio-button {
  display: inline-block;
}
hr {
  margin-top: 18px;
  margin-bottom: 18px;
  border-color: $grayLightColor;
}
::v-deep() .amenities .custom-control-inline {
  width: 50%;
  margin-right: 0;
}
::v-deep() .amenities .custom-control-label {
  font-size: 14px;
  padding-top: 2px;
}
.amenities svg {
  width: 20px;
  height: 20px;
  margin: 0 5px;
}
.filter-icon {
  width: 20px;
  height: 20px;
}
::v-deep() .chart-container {
  min-height: 200px;
}
::v-deep() .chart-container canvas {
  height: 200px !important;
}
@include media-breakpoint-down(sm) {
  .property-filter-wrapper {
    width: 30%;
    ::v-deep() .filter-button {
      width: 100%;
      border: 0;
      border-right: 1px solid $grayLightColor;
      // color: $grayLightColor;
      .filter-icon {
        fill: #fff !important;
      }
      &:focus,
      &:hover {
        box-shadow: none;
      }
      border-radius: 0;
    }
  }
}

@include media-breakpoint-down(sm) {
  .property-filter-wrapper {
    ::v-deep() .filter-button {
      border: 0;
    }
  }
}
[dir='rtl'] {
  @include media-breakpoint-down(sm) {
    .property-filter-wrapper {
      ::v-deep() .filter-button {
        border-right: 1px solid $grayLightColor;
        /* border-left: 0; */
      }
    }
  }
}
.filter-text {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #111;
  margin: 0 4px;
}
</style>
