<template>
  <div class="sort-container">
    <!-- short term and long term -->
    <!-- <b-dropdown right>
      <template #button-content>
        <span>{{ selected && selected.name }}</span>
        <img
          width="16px"
          height="16px"
          class="arrow-icon"
          :src="require('@/assets/images/sidebar/CaretRight.svg')" />
      </template>
      <b-dropdown-item
        v-for="(term, index) in notSelectedTerms"
        :key="index"
        @click="sortTerm(term)">
        {{ term.name }}
      </b-dropdown-item>
    </b-dropdown> -->
  </div>
</template>

<script>
export default {
  name: 'PropertySortTerm',
  data() {
    return {
      terms: [
        {
          name: this.dir === 'ltr' ? 'Short term' : 'قصير المدى',
          id: 0,
        },
        {
          name: this.dir === 'ltr' ? 'Long term' : 'طويل المدى',
          id: 1,
        },
      ],
      selected: null,
    };
  },
  computed: {
    notSelectedTerms() {
      return (
        this.terms?.filter((t) => t.id !== this.selected?.id) || this.terms
      );
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
  methods: {
    sortTerm(term) {
      this.selected = term;
    },
  },
  mounted() {
    this.selected = this.terms[0];
  },
};
</script>

<style lang="scss">
.sort-container {
  width: 230px;

  .dropdown {
    width: 100%;

    .btn {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 6px 12px;
      color: #111;
      font-weight: bold;
      background-color: transparent;
      border: 2px solid #111;
      border-radius: 8px;

      img {
        transform: rotate(90deg);
      }
    }
  }
}
</style>
