<template>
  <div class="filter-city">
    <!-- filter city btn market place -->
    <!-- <rent-select
      v-if="propertiesData"
      :options="propertiesData.rootLocations"
      @input="filterCity"
      id="citySelect"
      :title="$t('marketplace.filter_by_city')"
      :iconName="require(`@/assets/svg-sprite/svg/city.svg`)"
      :placeholder="$t('marketplace.filter_by_city')"
      label="location_name"
      value="location_id"
      :defaultValue="selected">
    </rent-select> -->
  </div>
</template>

<script>
import rentSelect from '~/components/shared/form/rentSelect';
export default {
  name: 'PropertyFilterByCity',
  components: {
    rentSelect,
  },
  data() {
    return {
      propertiesData: null,
      selected: null,
    };
  },
  methods: {
    filterCity(city) {
      const query = { ...this.$route.query, city: city.location_name };
      this.$router.push({ path: '/properties', query });
    },
  },
  async created() {
    const { Data } = await this.$axios.$get('/getPropMasterData');
    this.propertiesData = Data;
    if (this.$route.query.city) {
      this.selected = Data?.rootLocations.find(
        (city) => city.location_name === this.$route.query.city
      );
    }
  },
};
</script>

<style lang="scss">
.filter-city {
  width: 270px;

  .rent-select {
    .dropdown-container {
      border: 2px solid #111;
      font-weight: bold;
      height: 40px;
      align-items: center;
      line-height: unset;
      cursor: pointer;

      span,
      img {
        cursor: pointer;
      }
    }
  }

  #desktop-option-container {
    width: 270px;
  }
}
</style>
